import React, { Component, Fragment } from "react";
import "../App.css";
const imgPossition = [
  {
    top: "-8px",
    left: "-8px",
    width: "42px"
  },
  {
    top: "10px",
    left: "-70px",
    width: "92px"
  },
  {
    top: "-10px",
    left: "-60px",
    width: "65px"
  },
  {
    top: "0px",
    left: "-17px",
    width: "31px"
  },
  {
    top: "-48px",
    left: "-80px",
    width: "166px"
  },
  {
    top: "34px",
    left: "54px",
    width: "81px"
  },
  {},
  {
    top: "-40px",
    left: "-50px",
    width: "85px"
  }
];

export default class Cell extends Component {
  handleClick = () => this.props.setEndGame();

  render() {
    const { number, isTarget } = this.props;

    return (
      <Fragment>
        {number !== 7 ? (
          <img
            src={`./images/img_${number}.png`}
            style={imgPossition[number - 1]}
            alt="icon"
          />
        ) : null}
        <div className="hole">
          {isTarget === number && (
            <div onClick={this.handleClick} className="target" />
          )}
        </div>
      </Fragment>
    );
  }
}
