import React, { Component } from "react";
import Cell from "./Cell";
import "../App.css";

export default class Row extends Component {
  render() {
    const { cells, isTarget, setEndGame } = this.props;

    return (
      <div className="row">
        {Object.values(cells).map((isCell, index) =>
          isCell ? (
            <div
              key={index}
              className="cell"
              onMouseEnter={this.props.handleFocusEnter(isCell)}
              onMouseLeave={this.props.handleFocusLeave} // опционально
            >
              <Cell
                key={index}
                number={isCell}
                isTarget={isTarget}
                setEndGame={setEndGame}
              />
            </div>
          ) : (
            <div className="cell" key={index} />
          )
        )}
      </div>
    );
  }
}
