import React, { Component } from "react";
import Row from "./components/Row";
import "./App.css";

const messages = [
  "Я не сечин, просто очень похож",
  "Я занят",
  "Никого нет дома",
  "Сечина нет!",
  "Давайте я дам письменное разрешение!",
  "Не могу прийти, нужно согласовать графики",
  "Я встречаюсь с Медведевым"
];

const numberOfTargets = 8; // колво целей
const interval = 2000; // интервал появления цели

class App extends Component {
  state = {
    endGame: false,
    cellOnFocus: null,
    numberOfTargets: numberOfTargets,
    currentTarget: Math.floor(Math.random() * numberOfTargets + 1),
    message: messages[Math.floor(Math.random() * messages.length)],
    scheme: [
      {
        0: 1,
        1: false,
        2: false,
        3: false,
        4: 2
      },
      {
        0: false,
        1: false,
        2: 3,
        3: false,
        4: false
      },
      {
        0: false,
        1: false,
        2: false,
        3: false,
        4: 4
      },
      {
        0: false,
        1: false,
        2: 5,
        3: false,
        4: false
      },
      {
        0: 6,
        1: false,
        2: false,
        3: false,
        4: false
      },
      {
        0: false,
        1: false,
        2: 7,
        3: false,
        4: 8
      }
    ]
  };

  componentDidMount() {
    const { gameEnd } = this.state;

    if (!gameEnd) {
      setInterval(() => {
        this.setState({
          message: this.getRandomMessage(),
          currentTarget: this.getRandomNumber()
        });
      }, interval);
    }
  }

  handleFocusEnter = number => ev => {
    this.setState({ cellOnFocus: number });
  };

  handleFocusLeave = () => {
    this.setState({ cellOnFocus: null });
  };

  getRowOnFocus = cell => {
    switch (cell) {
      case 1 || 6:
        return [1, 6];
      case 2 || 4 || 8:
        return [2, 4, 8];
      case 3 || 5 || 7:
        return [3, 5, 7];
      default:
        return [];
    }
  };

  getRandomNumber = () => {
    // логика =>> рандом не может повторяться и совпасть с номером ячейки того ряда, на ячейке которого находится в данный момент курсор мышки
    const { cellOnFocus, currentTarget } = this.state;
    const getRandom = () =>
      Math.floor(Math.random() * this.state.numberOfTargets + 1);
    let randomNumber = getRandom();

    const getFocusRow = this.getRowOnFocus(cellOnFocus);
    while (
      randomNumber === currentTarget ||
      getFocusRow.indexOf(randomNumber) >= 0
    ) {
      randomNumber = getRandom();
    }

    return randomNumber;
  };

  getRandomMessage = () => {
    // Сообщение каждый раз меняется на рандомное новое из массива messages
    const getRandom = () =>
      messages[Math.floor(Math.random() * messages.length)];
    let randomMessage = getRandom();

    while (randomMessage === this.state.message) {
      randomMessage = getRandom();
    }

    return randomMessage;
  };

  setEndGame = () => {
    this.setState({ endGame: true });
  };

  restartGame = () => {
    this.setState({ endGame: false });
  };

  render() {
    const { message, scheme, currentTarget, endGame } = this.state;

    return (
      <div className="App">
        <div className="App__container">
          {endGame ? (
            <div className="end-game">
              <div className="end-game__content">
                <h3 className="end-game__text">Congratulations, you won!</h3>
                <button
                  className="end-game__btn"
                  onClick={this.restartGame}
                >
                  Start again
                </button>
              </div>
            </div> // Компонент окончания игры
          ) : (
            <div className="grid">
              {scheme.map((item, index) => (
                <Row
                  key={index}
                  cells={item}
                  isTarget={currentTarget}
                  setEndGame={this.setEndGame}
                  handleFocusEnter={this.handleFocusEnter}
                  handleFocusLeave={this.handleFocusLeave}
                />
              ))}
              <div className="message">{message}</div>
              <div className="footer">
                <button
                  className="button"
                  style={{ backgroundColor: "#B78B5C" }}
                >
                  Да он не уловим!
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default App;
